.Task {
  border-bottom: 1px solid #888; }

.Task.isDragging {
  opacity: 0.25; }

.Task.dropHere {
  border-top: 3px solid #ccf; }

.Task .taskInfo {
  background: #112;
  background: -webkit-gradient(linear, left top, left bottom, from(#112), to(#113));
  background: -webkit-linear-gradient(top, #112 0%, #113 100%);
  background: -o-linear-gradient(top, #112 0%, #113 100%);
  background: linear-gradient(to bottom, #112 0%, #113 100%); }

.Task.editMode > .taskInfo {
  -webkit-box-shadow: inset 0px -3px 5px -1px #ccf;
          box-shadow: inset 0px -3px 5px -1px #ccf; }



