.EditTaskTitle {
  margin-right: 165px; }

.EditTaskTitle input {
  background: none;
  border: none;
  color: inherit;
  font-size: inherit;
  line-height: inherit;
  padding: 0;
  width: 100%;
  -webkit-transition: -webkit-box-shadow .2s ease-in;
  transition: -webkit-box-shadow .2s ease-in;
  -o-transition: box-shadow .2s ease-in;
  transition: box-shadow .2s ease-in;
  transition: box-shadow .2s ease-in, -webkit-box-shadow .2s ease-in; }

.EditTaskTitle.saved input {
  -webkit-box-shadow: inset 0px -3px 5px -3px #cfc;
          box-shadow: inset 0px -3px 5px -3px #cfc; }

.EditTaskTitle input:focus {
  outline: none;
  -webkit-box-shadow: inset 0px -3px 5px -3px #ccf;
          box-shadow: inset 0px -3px 5px -3px #ccf; }



