.Header {
  background-color: #333;
  height: 60px;
  color: #fff;
  position: relative; }

.Header-logo {
  -webkit-animation: Header-logo-spin infinite 20s linear;
          animation: Header-logo-spin infinite 20s linear;
  height: 50px;
  margin: 5px; }

@-webkit-keyframes Header-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes Header-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

.Header h2 {
  margin: 0;
  display: inline-block;
  height: 60px;
  line-height: 60px;
  position: absolute;
  left: 60px;
  top: 0; }



